import { useState } from '@wordpress/element';
import {
  useBlockProps,
  RichText,
  InspectorControls,
  MediaUpload,
  MediaUploadCheck
} from '@wordpress/block-editor';
import { Button, ToggleControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';

import ContentPanel from './controls/ContentPanel';
import VideoHelpPanel from './controls/VideoHelpPanel';

const Edit = ({ attributes, setAttributes }) => {
  const { title, isBlockLine, second_title, description, imageData, imageId, bgData, bgId } = attributes;

  const [isPreview, setIsPreview] = useState(true);

  const togglePreview = () => {
    setIsPreview(!isPreview);
  };

  const blockProps = useBlockProps({
    className: 'block-style'
  });

  // Handler - image
  const onSelectImage = (media) => {
    setAttributes({
      imageId: media.id,
      imageData: {
        url: media.url,
        alt: media.alt || '',
        responsive: media.responsive || {
          webp: '',
          jpg: '',
          default: media.url,
        }
      }
    });
  };

  const onRemoveImage = () => {
    setAttributes({
      imageId: 0,
      imageData: {
        url: '',
        alt: '',
        responsive: {
          webp: '',
          jpg: '',
          default: '',
        }
      }
    });
  };

  // Handler - bg
  const onSelectBg = (media) => {
    setAttributes({
      bgId: media.id,
      bgData: {
        url: media.url,
        alt: media.alt || '',
        responsive: media.responsive || {
          webp: '',
          jpg: '',
          default: media.url,
        }
      }
    });
  };

  const onRemoveBg = () => {
    setAttributes({
      bgId: 0,
      bgData: {
        url: '',
        alt: '',
        responsive: {
          webp: '',
          jpg: '',
          default: '',
        }
      }
    });
  };

  return (
    <>
      <InspectorControls>
        <VideoHelpPanel />
        <ContentPanel attributes={attributes} setAttributes={setAttributes} />
      </InspectorControls>

      <div {...blockProps}>
        <div className="advanced-block">
          <div className="block-info">
            <span className="block-info-title"> Block 01</span>
            <ToggleControl
              label={isPreview ? __(' ', 'theme') : __(' ', 'theme')}
              checked={isPreview}
              onChange={togglePreview}
            />
          </div>

          {!isPreview && (
            <div></div>
          )}

          {isPreview && (
            <div className="advanced-block-content">
              <div className="advanced-block-text">
                <>
                  <label htmlFor="rich-title" className="my-rich-text__label"></label>
                  <RichText
                    id="rich-title"
                    tagName="div"
                    label=""
                    value={title}
                    onChange={(value) => setAttributes({ title: value })}
                    placeholder={__('...', 'theme')}
                    allowedFormats={[]}
                  />
                </>

                <>
                  <label htmlFor="rich-title" className="my-rich-text__label">  </label>
                  <ToggleControl
                    label={isBlockLine ? __('  ', 'theme') : __('  ', 'theme')}
                    checked={isBlockLine}
                    onChange={(value) => setAttributes({ isBlockLine: value })}
                  />
                </>

                <>
                  <label htmlFor="rich-second_title" className="my-rich-text__label"> </label>
                  <RichText
                    id="rich-second_title"
                    tagName="div"
                    value={second_title}
                    onChange={(value) => setAttributes({ second_title: value })}
                    placeholder={__(' ...', 'theme')}
                    allowedFormats={[]}
                  />
                </>

                <>
                  <label htmlFor="rich-description" className="my-rich-text__label"></label>
                  <RichText
                    id="rich-description"
                    tagName="div"
                    value={description}
                    onChange={(value) => setAttributes({ description: value })}
                    placeholder={__('...', 'theme')}
                    allowedFormats={['core/bold', 'core/italic', 'core/link']}
                  />
                </>

              </div>

              <MediaUploadCheck>
                <MediaUpload
                  onSelect={onSelectImage}
                  allowedTypes={['image']}
                  value={imageId}
                  render={({ open }) => (
                    <div className="advanced-block-image advanced-block-image-48">
                      <div className="label-image"> ()</div>
                      {imageData.url ? (
                        <>
                          <img
                            src={imageData.url}
                            className="advanced-image-preview"
                            alt=""
                            style={{ borderRadius: '8px' }}
                          />
                          <div style={{ display: 'flex', gap: '6px', marginTop: '4px' }}>
                            <Button onClick={open} variant="secondary" size="small">
                               {__('', 'theme')}
                            </Button>
                            <Button
                              onClick={onRemoveImage}
                              variant="tertiary"
                              size="small"
                              isDestructive
                            >
                               {__('', 'theme')}
                            </Button>
                          </div>
                        </>
                      ) : (
                        <Button onClick={open} variant="primary">
                           {__(' ', 'theme')}
                        </Button>
                      )}
                    </div>
                  )}
                />
              </MediaUploadCheck>

              <MediaUploadCheck>
                <MediaUpload
                  onSelect={onSelectBg}
                  allowedTypes={['image']}
                  value={bgId}
                  render={({ open }) => (
                    <div className="advanced-block-image advanced-block-image-48">
                      <div className="label-image"> ()</div>
                      {bgData.url ? (
                        <>
                          <img
                            src={bgData.url}
                            className="advanced-image-preview"
                            alt=""
                            style={{ borderRadius: '8px' }}
                          />
                          <div style={{ display: 'flex', gap: '6px', marginTop: '4px' }}>
                            <Button onClick={open} variant="secondary" size="small">
                               {__('', 'theme')}
                            </Button>
                            <Button
                              onClick={onRemoveBg}
                              variant="tertiary"
                              size="small"
                              isDestructive
                            >
                               {__('', 'theme')}
                            </Button>
                          </div>
                        </>
                      ) : (
                        <Button onClick={open} variant="primary">
                           {__(' ', 'theme')}
                        </Button>
                      )}
                    </div>
                  )}
                />
              </MediaUploadCheck>
            </div>
          )}
        </div>
      </div>
    </>
  );
};

export default Edit;
